import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-listbox-item';
const CSS_STATE = 'vds-state';

const ListboxItem = forwardRef(({
  active,
  children,
  className,
  disabled,
  focus,
  listItemValue,
  role,
  selected,
  ...remainingProps
}, ref) => (
  <li
    aria-disabled={Boolean(disabled) ? 'true' : undefined}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_STATE}--disabled`]: Boolean(disabled),
      [`${CSS_STATE}--focus`]: Boolean(focus),
      [`${CSS_STATE}--selected`]: Boolean(selected)
    }, className)}
    data-list-item-value={listItemValue}
    ref={ref}
    role={role}
    tabIndex={Boolean(disabled) ? '-1' : undefined}
    {...remainingProps}
  >
    {children}
  </li>
));

ListboxItem.propTypes = {
  /**
   * Active
   */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Focus
   */
  focus: bool,

  /**
   * List item value
   */
  listItemValue: string,

  /**
   * Role
   */
  role: string,

  /**
   * Selected
   */
  selected: bool
};

ListboxItem.defaultProps = {
  role: 'option'
};

ListboxItem.displayName = 'ListboxItem';

export default ListboxItem;
