import { cloneElement, forwardRef } from 'react';
import { bool, element, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-listbox-item-container';
const CSS_STATE = 'vds-state';

const ListboxItemContainer = forwardRef(({
  children,
  className,
  disabled,
  ...remainingProps
}, ref) => (
  cloneElement(children, {
    className: classnames(CSS_PREFIX, {
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className),
    ref,
    ...remainingProps
  })
));

ListboxItemContainer.propTypes = {
  /**
   * @ignore
   */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool
};

ListboxItemContainer.displayName = 'ListboxItemContainer';

export default ListboxItemContainer;
