import React, { Children, cloneElement, useCallback, useEffect, useRef, useState, Fragment } from 'react';
import { element, number, oneOf, oneOfType, shape, string } from 'prop-types';
import classnames from 'classnames';
import shortid from 'shortid';
import { Manager, Reference, Popper } from 'react-popper';
import Menu from '../menu';

const CSS_PREFIX = 'vds-navmenu';
const CSS_POPPER_CONTAINER = 'vds-popper-container';

const keyCode = {
  DOWN: 40,
  END: 35,
  ESC: 27,
  HOME: 36,
  PAGEDOWN: 34,
  PAGEUP: 33,
  RETURN: 13,
  SPACE: 32,
  TAB: 9,
  UP: 38
};

const Navmenu = ({
  children,
  className,
  id = shortid.generate(),
  menu,
  modifiers,
  placement,
  ...remainingProps
}) => {
  const [open, setOpen] = useState(false);
  const [focusedIndex, setFocusedIndex] = useState(0);
  const [focusableItems, setFocusableItems] = useState([]);
  const [focusableItemsTextContent, setFocusableItemsTextContent] = useState({});
  const [disabledFocusReturn, setDisabledFocusReturn] = useState(true);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  const listboxRef = useRef();
  const buttonRef = useRef();

  useEffect(() => {
    if (listboxRef.current) {
      const listBoxChildren = listboxRef.current.querySelectorAll(
        'button:not([disabled]):not(.vds-state--disabled), [href]:not(.vds-state--disabled)'
      );

      setFocusableItems(listBoxChildren);

      setFocusableItemsTextContent(Array.from(listBoxChildren).map(
        (node, i) => ({
          char: node.textContent.trim().toLowerCase().substring(0, 1),
          index: i
        })
      ));
    }
  }, []);

  const handleClick = useCallback(e => {
    const isButton = e.target.tagName.toLowerCase() === 'button';
    const isLink = e.target.tagName.toLowerCase() === 'a';

    setOpen(!open);
    if (!isButton && !isLink) {
      e.preventDefault();
    }
  }, [open, setOpen]);

  useEffect(() => {
    if (!open) {
      return;
    }
    const handleClickOutside = e => {
      const menuEl = document.querySelector(`#${CSS_PREFIX}-menu-${uid}`);
      if (buttonRef.current.contains(e.target) || menuEl.contains(e.target)) {
        // let the button handle its own clicking functionality
        // and don't close if the menu itself is clicked
        return;
      }

      // the click is outside, close the menu
      handleClick(e);
    };
    document.addEventListener('click', handleClickOutside);
    document.addEventListener('touchend', handleClickOutside);

    return () => {
      document.removeEventListener('click', handleClickOutside);
      document.removeEventListener('touchend', handleClickOutside);
    };
  }, [open, handleClick, buttonRef, uid]);

  useEffect(() => {
    if (open) {
      focusableItems[focusedIndex].focus();
    }

    if (!open && buttonRef.current && !disabledFocusReturn) {
      buttonRef.current.focus();
      setDisabledFocusReturn(false);
    }
  }, [disabledFocusReturn, focusedIndex, focusableItems, open]);


  const handleKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const count = focusableItems.length;
    const isButton = e.currentTarget === buttonRef.current;
    const isListBox = e.currentTarget === listboxRef.current;
    const char = e.key.toLowerCase();
    const isPrintableChar = char.length === 1 && char.match(/\S/);
    let focusId = 0;

    if (isListBox) {
      setDisabledFocusReturn(false);
    }

    if (e.ctrlKey || e.altKey || e.metaKey || (e.keyCode === keyCode.SPACE) || (e.keyCode === keyCode.RETURN)) {
      return;
    }

    if (code === keyCode.END || code === keyCode.HOME) {
      e.preventDefault();

      if (isButton) {
        setOpen(true);
      }

      if (count) {
        focusId = code === keyCode.END ? count - 1 : 0;
        setFocusedIndex(focusId);
        focusableItems[focusId].focus();
      }
    }

    if (code === keyCode.UP || code === keyCode.DOWN) {
      e.preventDefault();

      if (isButton) {
        setOpen(true);
        focusId = code === keyCode.UP
          ? focusedIndex || count - 1
          : focusedIndex || 0;
      }

      if (isListBox && count) {
        focusId = code === keyCode.UP
          ? focusedIndex - 1 < 0 ? count - 1 : focusedIndex - 1
          : (focusedIndex + 1) % count;
      }

      if (count) {
        setFocusedIndex(focusId);
        focusableItems[focusId].focus();
      }
    }

    if (code === keyCode.ENTER) {
      setOpen(isButton);
    }

    if (code === keyCode.ESC) {
      e.preventDefault();
      setOpen(false);
    }

    // Tab
    if (code === keyCode.TAB && isListBox) {
      setDisabledFocusReturn(true);
      setOpen(false);
    }

    if (isPrintableChar && open) {
      const start = focusedIndex + 1 === count ? 0 : focusedIndex + 1;
      const filteredTextContent = focusableItemsTextContent.filter(charObj =>
        charObj.char === char && charObj.index !== focusedIndex
      ).map(obj => obj.index);

      let matchIndex = filteredTextContent.indexOf(start);
      matchIndex = (matchIndex > -1)
        ? filteredTextContent[matchIndex]
        : filteredTextContent[0] === undefined ? -1 : filteredTextContent[0];

      if (matchIndex > -1) {
        setFocusedIndex(matchIndex);
        focusableItems[matchIndex].focus();
      }
    }
  };

  return (
    <Manager>
      <div className={classnames(CSS_PREFIX, className)} {...remainingProps}>
        <Popper modifiers={modifiers} placement={placement}>
          {({ ref, style, scheduleUpdate }) => (
            <Fragment>
              <Reference>
                {({ ref }) => (
                  <span ref={ref} className={CSS_POPPER_CONTAINER}>
                    {cloneElement(children, {
                      active: open,
                      'aria-controls': `${CSS_PREFIX}-listbox-${uid}`,
                      'aria-expanded': open ? 'true' : undefined,
                      'aria-haspopup': menu ? 'true' : 'false',
                      id: uid,
                      onClick: e => {
                        // the popper position is not always positioned properly until it re-renders after being shown
                        scheduleUpdate();
                        handleClick(e);
                      },
                      onKeyDown: handleKeyDown,
                      ref: buttonRef
                    })}
                  </span>
                )}
              </Reference>
              <Menu
                id={`${CSS_PREFIX}-menu-${uid}`}
                position="absolute"
                ref={ref}
                show={open}
                style={{ ...style, overflowY: 'inherit' }}
              >
                {cloneElement(menu, {
                  'aria-labelledby': uid,
                  children: Children.map(menu.props.children, (child, k) =>
                    cloneElement(child, {
                      children: Children.map(child.props.children, (c, i) =>
                        cloneElement(c, {
                          key: `${uid}-${k}-${i}`,
                          role: 'menuitem',
                          tabIndex: '-1'
                        })
                      ),
                      key: `${uid}-${k}`,
                      role: 'none'
                    })
                  ),
                  id: `${CSS_PREFIX}-listbox-${uid}`,
                  onClick: handleClick,
                  onKeyDown: handleKeyDown,
                  ref: listboxRef,
                  role: 'menu'
                })}
              </Menu>
            </Fragment>
          )}
        </Popper>
      </div>
    </Manager>
  );
};

Navmenu.propTypes = {
  /**
   * @ignore
   */
  children: element,

  /**
   * @ignore
   */
  className: string,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Menu
   */
  menu: element.isRequired,

  /**
   * Modifiers
   */
  modifiers: shape({}),

  /**
   * Placement
   */
  placement: oneOf([
    'auto',
    'bottom-end',
    'bottom-start',
    'bottom',
    'left-end',
    'left-start',
    'left',
    'right-end',
    'right-start',
    'right',
    'top-end',
    'top-start',
    'top'
  ])
};

Navmenu.defaultProps = {
  modifiers: {
    offset: {
      enabled: true,
      offset: '0'
    }
  },
  placement: 'bottom-start'
};

Navmenu.displayName = 'Navmenu';

export default Navmenu;
