import React, { forwardRef } from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-menu';

const Menu = forwardRef(({
  children,
  className,
  position,
  show,
  ...remainingProps
}, ref) => (
  <div
    className={classnames({
      [`${CSS_PREFIX}`]: !position,
      [`${CSS_PREFIX}--${position}`]: Boolean(position),
      [`${CSS_PREFIX}--show`]: Boolean(show)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

Menu.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Position
   */
  position: oneOf(['absolute', 'fixed']),

  /**
   * Show
   */
  show: bool
};

Menu.displayName = 'Menu';

export default Menu;
