import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-listbox';

const Listbox = forwardRef(({
  children,
  className,
  multiselect,
  role,
  ...remainingProps
}, ref) => (
  <ul
    className={classnames(CSS_PREFIX, {
      'vds-select--multi': Boolean(multiselect)
    }, className)}
    ref={ref}
    role={role}
    {...remainingProps}
  >
    {children}
  </ul>
));

Listbox.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Multi select
   */
  multiselect: bool,

  /**
   * Role
   */
  role: string
};

Listbox.defaultProps = {
  role: 'listbox'
};

Listbox.displayName = 'Listbox';

export default Listbox;
